import EditableTableV2, { Column } from "components/EditableTableV2";
import ErrorDialog from "components/ErrorDialog";
import Loader from "components/Loader";
import ReadOnlyTable from "components/ReadOnlyTable";
import RichTextEditor from "components/RichTextEditor";
import SaveComponent from "components/Save/SaveComponent";
import Section from "components/Section";
import SimpleDropDown from "components/SimpleDropDown";
import SimpleTab from "components/SimpleTab";
import SkeletonCustom from "components/Skeleton";
import Text from "components/Text";
import TextButton from "components/TextButton/TextButton";
import ToastCustom from "components/Toast";
import { useTextConverter } from "hook/CustomHooks";
import { DropdownChangeParams } from "primereact/dropdown";
import { ToastPositionType } from "primereact/toast";
import React, { useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import StaticMasterDataService from "services/masterdata/StaticMaster/StaticMasterDataService";
import DedupeService from "services/transaction/DedupeService";
import SimpleReactValidator from "simple-react-validator";
import {
  formatDateWithTime,
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
  validateGetAPIResponse,
  validatePostAPIResponse,
  validatePutAPIResponse,
} from "utils/CommonUtil";
import {
  APPLICANT_TYPE,
  CURRENT_STAGE_STATUS,
  DEDUPE_CHECK,
  PAGE_MODE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
} from "utils/Constant";

interface ICustomerTabInfo {
  tabName: string;
  key: string;
  tabType: string;
}
interface IApplicantInfo {
  uid: string;
  type: string;
  firstName: string;
  middleName: string;
  lastName: string;
  mobileNo: string;
  lanId: string;
}
interface IPageData {
  loanUid: string;
  stageUid: string;
  nameSearch: {
    nameSearchPageMode: string;
    applicantsInLoan: IApplicantInfo[];
  };
  kycSearch: {
    kycSearchPageMode: string;
  };
  finalStatus: {
    uid: string | null;
    isFinalStatusLoading: boolean;
    finalStatusPageMode: string;
    status: string;
    statusEditorData: string;
  };
}

interface IApplicantKycDocInfo {
  uid: string;
  applicantType: string;
  documentTypeName: string;
  documentTypeCode: string;
  documentNo: string;
  lanId: string;
}

const initialApplicantInfo: IApplicantInfo = {
  uid: "",
  type: "",
  firstName: "",
  middleName: "",
  lastName: "",
  mobileNo: "",
  lanId: "",
};

const initialApplicantKycDocInfo: IApplicantKycDocInfo = {
  uid: "",
  applicantType: "",
  documentTypeName: "",
  documentTypeCode: "",
  documentNo: "",
  lanId: "",
};

const initialPageData: IPageData = {
  loanUid: "",
  stageUid: "",
  nameSearch: {
    nameSearchPageMode: PAGE_MODE.VIEW,
    applicantsInLoan: [],
  },
  kycSearch: {
    kycSearchPageMode: PAGE_MODE.VIEW,
  },
  finalStatus: {
    uid: null,
    isFinalStatusLoading: false,
    status: "",
    statusEditorData: "",
    finalStatusPageMode: PAGE_MODE.VIEW,
  },
};

const APPLICANT_TYPE_TABS_NAMES = {
  [`${APPLICANT_TYPE.PRIMARY}`]: "Applicant",
  [`${APPLICANT_TYPE.CO_APPLICANT}`]: "Co-Applicant",
  [`${APPLICANT_TYPE.GUARANTOR}`]: "Guarantor",
};

const GENDER: { [key: string]: string } = {};

const PREVIOUS_STATUS: { [key: string]: string } = {
  VALID: "Valid",
  INVALID: "Invalid",
};

const PageCustomerDedupe = () => {
  // --------------------- states ---------------------- //

  const params: { loanUid?: string; stageUid?: string } = useParams();

  const [isPageLoading, setIsPageLoading] = useState<boolean>(true);
  const [pageData, setPageData] = useState<IPageData>(initialPageData);
  const [allLinkedApplicantsList, setAllLinkedApplicantList] = useState<IApplicantInfo[]>([]);
  const [currentApplicantInfo, setCurrentApplicantInfo] =
    useState<IApplicantInfo>(initialApplicantInfo);
  const [currentApplicantKycDocsInfo, setCurrentApplicantKycDocsInfo] = useState<
    IApplicantKycDocInfo[]
  >([initialApplicantKycDocInfo]);
  const [activeKycDocForDedupe, setActiveKycDocForDedupe] = useState<IApplicantKycDocInfo>(
    initialApplicantKycDocInfo
  );
  const [activeIndex, setActiveIndex] = useState<number>(0);
  const [customerTabsListForDedupe, setCustomerTabsListForDedupe] = useState<ICustomerTabInfo[]>(
    []
  );

  // error handling states
  const [position, setPosition] = useState<ToastPositionType | undefined>("bottom-right"); //NOSONAR
  const [toastMessage, setToastMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [triggerToast, setTriggerToast] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);

  // --------------------- ref ---------------------------- //
  const toastMessageRef = useRef(null);
  const tabRef = useRef<any>(null);
  const nameSearchTableRef = useRef<any>(null);
  const nameSearchMatchTableRef = useRef<any>(null);
  const kycSearchTableRef = useRef<any>(null);
  const kycSearchMatchTableRef = useRef<any>(null);
  const statusEditorRef = useRef<any>(null);

  // --------------------- custom hooks ---------------------------- //
  const textConverter = useTextConverter();

  // --------------------- validator initialization ---------------------- //

  const [, forceUpdate] = useState<boolean>(false); //NOSONAR

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev: boolean) => !prev) },
    })
  );

  // --------------------- global variables ---------------------- //

  /*
    --------------------------------------------------------------------------------------------------
    useEffect: 
    --------------------------------------------------------------------------------------------------
  */

  useEffect(() => {
    if (params?.loanUid && params?.stageUid) {
      setPageData((prev: IPageData) => ({
        ...prev,
        loanUid: params.loanUid ?? "",
        stageUid: params.stageUid ?? "",
      }));
    }
  }, [params?.stageUid, params?.loanUid]);

  useEffect(() => {
    init();
  }, []);

  useEffect(() => {
    if (pageData.loanUid) {
      getLinkedLoanApplicants();
    }
  }, [pageData.loanUid]);

  useEffect(() => {
    if (currentApplicantInfo?.uid && currentApplicantInfo?.lanId) {
      getLinkedLoanApplicantsKycInfo();
      getCustomerDedupeFinalStatus();
    }
  }, [pageData.stageUid, currentApplicantInfo?.uid]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  /*
    --------------------------------------------------------------------------------------------------
    Ref: Exposing essential functions for external control
    --------------------------------------------------------------------------------------------------
  */

  /*
    -------------------------------------------------------------------------------------------------------
    Service Calls: For Getting Data from service/API
    -------------------------------------------------------------------------------------------------------
  */

  const getLinkedLoanApplicants = async () => {
    const search = `&loan.uid=${pageData.loanUid}&applicantType:in=${[
      APPLICANT_TYPE.PRIMARY,
      APPLICANT_TYPE.CO_APPLICANT,
      APPLICANT_TYPE.GUARANTOR,
    ]}&isActive=${true}`;

    try {
      const res = await DedupeService.getApplicants(search);

      if (validateGetAPIResponse(res)) {
        const linkedApplicantsList: IApplicantInfo[] = res?.data?.results?.map((record: any) => {
          return {
            uid: record?.uid ?? "",
            type: record?.applicantType ?? "",
            firstName: record?.firstName ?? "",
            middleName: record?.middleName ?? "",
            lastName: record?.lastName ?? "",
            mobileNo: record?.mobileNo ?? "",
            lanId: record?.loan?.lanId ?? "",
          };
        });

        const preparedTabsForCurrentLoan = linkedApplicantsList.map((record: any) => ({
          tabName: `${APPLICANT_TYPE_TABS_NAMES[record.type] ?? ""} - ${record.firstName}`,
          key: record.uid,
          tabType: record.type,
        }));

        setPageData((prev: IPageData) => ({
          ...prev,
          nameSearch: {
            ...prev.nameSearch,
            applicantsInLoan: linkedApplicantsList,
          },
        }));
        setCustomerTabsListForDedupe(preparedTabsForCurrentLoan);
        setAllLinkedApplicantList(linkedApplicantsList);
        setCurrentApplicantInfo(linkedApplicantsList[activeIndex]);
        setIsPageLoading(false);
      }
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const getLinkedLoanApplicantsKycInfo = async () => {
    const search = `isActive=${true}&loan.uid=${pageData.loanUid}&applicant.uid=${
      currentApplicantInfo.uid
    }`;
    try {
      const res = await DedupeService.getApplicantsKyc(search);

      if (validateGetAPIResponse(res)) {
        const listOfKycDocs: IApplicantKycDocInfo[] = res?.data?.results?.map((record: any) => {
          return {
            uid: record?.uid ?? "",
            applicantType: record?.applicant?.applicantType ?? "",
            documentTypeName: record?.docType?.name ?? "",
            documentTypeCode: record?.docType?.code ?? "",
            documentNo: record?.docNo ?? "",
            lanId: record?.loan?.lanId,
          };
        });

        setCurrentApplicantKycDocsInfo(listOfKycDocs);
      }
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage);
    }
  };

  const getCustomerDedupeFinalStatus = async () => {
    const search = `linkedApplicantUid=${currentApplicantInfo.uid}&linkedLanId=${
      currentApplicantInfo.lanId
    }&type=${"Status"}`;
    setPageData((prev: IPageData) => {
      return {
        ...prev,
        finalStatus: {
          ...prev.finalStatus,
          isFinalStatusLoading: true,
        },
      };
    });
    try {
      const res = await DedupeService.getCustomerDedupeStatus(search);
      if (validateGetAPIResponse(res)) {
        //always one record will come in response in the results array
        const statusInfo = res.data?.results?.[0];
        if (statusInfo) {
          setPageData((prev: IPageData) => {
            return {
              ...prev,
              finalStatus: {
                ...prev.finalStatus,
                isFinalStatusLoading: false,
                uid: statusInfo?.uid ?? "",
                status: statusInfo?.action ?? "",
                statusEditorData: statusInfo?.remark ?? "",
              },
            };
          });
        } else {
          setPageData((prev: IPageData) => {
            return {
              ...prev,
              finalStatus: {
                ...prev.finalStatus,
                uid: null,
                isFinalStatusLoading: false,
                status: "",
                statusEditorData: "",
                finalStatusPageMode: PAGE_MODE.VIEW,
              },
            };
          });
        }
      }
    } catch (err) {
      console.log(err);
      setPageData((prev: IPageData) => {
        return {
          ...prev,
          finalStatus: {
            ...prev.finalStatus,
            isFinalStatusLoading: false,
          },
        };
      });
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const getGender = () => {
    StaticMasterDataService.getAll(0, 1000, "staticMasterType.code=GENDER&")
      .then((res) => {
        if (validateGetAPIResponse(res)) {
          res.data?.results?.forEach((gender: any) => {
            GENDER[`${gender.code}`] = gender.name;
          });
        }
      })
      .catch((err) => {
        console.log(err);
        setErrorMessage(parseStandardAPIErrorMessage(err));
      });
  };

  /* 
    --------------------------------------------------------------------------------------------------
    Validators:
    --------------------------------------------------------------------------------------------------
  */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;

    isValid = simpleValidator?.current?.allValid();
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  /*
    -----------------------------------------------------------------------------------------------
    Handlers: Event handler for onChange, onBlur etc
    -----------------------------------------------------------------------------------------------
  */

  const handleTabChange = (e: any) => {
    setActiveIndex(e.index);
    const currentApplicantDetails: IApplicantInfo[] = allLinkedApplicantsList.filter(
      (applicant: IApplicantInfo) => applicant.uid == customerTabsListForDedupe[e.index].key
    );
    setCurrentApplicantInfo(currentApplicantDetails[0]);
    setActiveKycDocForDedupe(initialApplicantKycDocInfo);
  };

  const handleStatusChange = (e: DropdownChangeParams) => {
    setPageData((prev: IPageData) => ({
      ...prev,
      finalStatus: {
        ...prev.finalStatus,
        status: e.target.value,
      },
    }));
  };

  /*
    -----------------------------------------------------------------------------------------------
    on-Handlers: Event handler for onClick, onToggle etc
    -----------------------------------------------------------------------------------------------
  */

  const onSearchCustomerForDedupe = async (rowData: IApplicantInfo) => {
    const payload = enrichCustomerSearchForDedupe(rowData);

    try {
      const res = await DedupeService.searchCustomerForDedupe(payload);

      if (validatePostAPIResponse(res)) {
        nameSearchMatchTableRef.current?.search();
      }
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const onSearchKycDocForDedupe = async (rowData: IApplicantKycDocInfo) => {
    setActiveKycDocForDedupe(rowData);
    const payload = enrichKycSearchForDedupe(rowData);

    try {
      const res = await DedupeService.searchCustomerKycForDedupe(payload);

      if (validatePostAPIResponse(res)) {
        kycSearchTableRef.current?.search();
      }
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const onSaveNameSearchMatchedResults = async () => {
    const payload = enrichNameSearchMatchedResults();

    try {
      const res = await DedupeService.updateDedupeMultiple(payload);

      if (validatePutAPIResponse(res)) {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
        setTriggerToast(!triggerToast);

        setPageData((prev) => ({
          ...prev,
          nameSearch: {
            ...prev.nameSearch,
            nameSearchPageMode: PAGE_MODE.VIEW,
          },
        }));

        nameSearchMatchTableRef.current?.search();
        nameSearchMatchTableRef.current?.clearAll();
      }
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const onEditNameSearch = () => {
    setPageData((prevState: IPageData) => {
      return {
        ...prevState,
        nameSearch: {
          ...prevState.nameSearch,
          nameSearchPageMode: PAGE_MODE.EDIT,
        },
      };
    });
  };

  const onCancelNameSearch = () => {
    setPageData((prevState: IPageData) => {
      return {
        ...prevState,
        nameSearch: {
          ...prevState.nameSearch,
          nameSearchPageMode: PAGE_MODE.VIEW,
        },
      };
    });
  };

  const onSaveKycSearchMatchedResults = async () => {
    const payload = enrichKycSearchMatchedResults();

    try {
      const res = await DedupeService.updateDedupeMultiple(payload);

      if (validatePutAPIResponse(res)) {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
        setTriggerToast(!triggerToast);
        setPageData((prev) => ({
          ...prev,
          kycSearch: {
            ...prev.kycSearch,
            kycSearchPageMode: PAGE_MODE.VIEW,
          },
        }));
        kycSearchMatchTableRef.current?.search();
        kycSearchMatchTableRef.current?.clearAll();
      }
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const onEditKYCSearch = () => {
    setPageData((prevState: IPageData) => {
      return {
        ...prevState,
        kycSearch: {
          ...prevState.kycSearch,
          kycSearchPageMode: PAGE_MODE.EDIT,
        },
      };
    });
  };

  const onCancelKYCSearch = () => {
    setPageData((prevState: IPageData) => {
      return {
        ...prevState,
        kycSearch: {
          ...prevState.kycSearch,
          kycSearchPageMode: PAGE_MODE.VIEW,
        },
      };
    });
  };

  const onSaveFinalStatus = async () => {
    if (!validateForm()) {
      return;
    }

    const payload = enrichDedupeFinalStatus();

    try {
      const res = await DedupeService.updateDedupeFinalStatus(payload);

      if (validatePutAPIResponse(res)) {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
        setTriggerToast(!triggerToast);
        setPageData((prev) => ({
          ...prev,
          finalStatus: {
            ...prev.finalStatus,
            finalStatusPageMode: PAGE_MODE.VIEW,
          },
        }));
      }
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const onEditFinalStatus = () => {
    setPageData((prevState: IPageData) => {
      return {
        ...prevState,
        finalStatus: {
          ...prevState.finalStatus,
          finalStatusPageMode: PAGE_MODE.EDIT,
        },
      };
    });
  };

  const onCancelFinalStatus = () => {
    getCustomerDedupeFinalStatus();
    setPageData((prevState: IPageData) => {
      return {
        ...prevState,
        finalStatus: {
          ...prevState.finalStatus,
          finalStatusPageMode: PAGE_MODE.VIEW,
        },
      };
    });
    resetValidator();
  };

  /* 
    -------------------------------------------------------------------------------------------------
    Helper Functions:
    -------------------------------------------------------------------------------------------------
  */
  const init = async () => {
    getGender();
    nameSearchMatchTableRef.current?.search();
  };

  const enrichCustomerSearchForDedupe = (rowData: IApplicantInfo) => {
    const payloadData = {
      firstName: rowData.firstName,
      middleName: rowData.middleName,
      lastName: rowData.lastName,
      mobileNo: rowData.mobileNo,
      stageUid: pageData.stageUid,
      applicantUid: rowData.uid,
      lanId: rowData.lanId,
    };
    return payloadData;
  };

  const enrichNameSearchMatchedResults = () => {
    const payloadData = nameSearchMatchTableRef.current
      ?.getUpdatedRecords()
      ?.map((updatedRecord: any) => ({
        uid: updatedRecord.uid,
        action: updatedRecord.action,
      }));

    return payloadData;
  };

  const enrichKycSearchMatchedResults = () => {
    const payloadData = kycSearchMatchTableRef.current
      ?.getUpdatedRecords()
      ?.map((updatedRecord: any) => ({
        uid: updatedRecord.uid,
        action: updatedRecord.action,
      }));

    return payloadData ? payloadData : [];
  };

  const enrichKycSearchForDedupe = (rowData: IApplicantKycDocInfo) => {
    const payloadData = {
      docTypeCode: rowData.documentTypeCode,
      docNo: rowData.documentNo,
      stageUid: pageData.stageUid,
      kycUid: rowData.uid,
      lanId: rowData.lanId,
    };

    return payloadData;
  };

  const enrichDedupeFinalStatus = () => {
    const payloadData = {
      uid: pageData.finalStatus.uid ? pageData.finalStatus.uid : null,
      isLinkage: false,
      linkedLanId: currentApplicantInfo.lanId,
      linkedApplicantUid: currentApplicantInfo.uid,
      action: pageData.finalStatus.status,
      remark: statusEditorRef.current?.editor?.getContent(),
      type: "Status",
    };

    return payloadData;
  };

  /* 
    ---------------------------------------------------------------------------------------------
      Sectional Render Functions: For Sub components / section wise renders
    ---------------------------------------------------------------------------------------------
  */

  const renderFirstName = (rowData?: any) => {
    if (rowData?.applicant) {
      return <Text className="" label={rowData?.applicant?.firstName ?? "-"} />;
    } else if (rowData?.kyc) {
      return <Text className="" label={rowData?.kyc?.applicant?.firstName ?? "-"} />;
    } else if (rowData?.blackList) {
      return <Text className="" label={rowData?.blackList?.firstName ?? "-"} />;
    } else {
      return <Text className="" label={rowData?.firstName ?? "-"} />;
    }
  };

  const renderMiddleName = (rowData?: any) => {
    if (rowData?.applicant) {
      return <Text className="" label={rowData?.applicant?.middleName ?? "-"} />;
    } else if (rowData?.kyc) {
      return <Text className="" label={rowData?.kyc?.applicant?.middleName ?? "-"} />;
    } else if (rowData?.blackList) {
      return <Text className="" label={rowData?.blackList?.middleName ?? "-"} />;
    } else {
      return <Text className="" label={rowData?.middleName ?? "-"} />;
    }
  };

  const renderLastName = (rowData?: any) => {
    if (rowData?.applicant) {
      return <Text className="" label={rowData?.applicant?.lastName ?? "-"} />;
    } else if (rowData?.kyc) {
      return <Text className="" label={rowData?.kyc?.applicant?.lastName ?? "-"} />;
    } else if (rowData?.blackList) {
      return <Text className="" label={rowData?.blackList?.lastName ?? "-"} />;
    } else {
      return <Text className="" label={rowData?.lastName ?? "-"} />;
    }
  };

  const renderMobileNo = (rowData?: any) => {
    if (rowData?.applicant) {
      return <Text className="" label={rowData?.applicant?.mobileNo ?? "-"} />;
    } else if (rowData?.kyc) {
      return <Text className="" label={rowData?.kyc?.applicant?.mobileNo ?? "-"} />;
    } else if (rowData?.blackList) {
      return <Text className="" label={rowData?.blackList?.mobileNo ?? "-"} />;
    } else {
      return <Text className="" label={rowData?.mobileNo ?? "-"} />;
    }
  };

  const renderNameSearchAction = (rowData?: any) => {
    return (
      <TextButton
        className={"text-primary-sapphireBlue-600"}
        label={"text.masterData.types.search"}
        onClick={() => onSearchCustomerForDedupe(rowData)}
        disabled={pageData.nameSearch.nameSearchPageMode === PAGE_MODE.VIEW}
      />
    );
  };

  const renderMatchingList = (rowData?: any) => {
    return <Text className="" label={rowData?.matchingList ?? "-"} />;
  };

  const renderFileStatus = (rowData?: any) => {
    if (rowData?.kyc) {
      return <Text className="" label={rowData?.kyc?.loan?.loanStatus ?? "-"} />;
    } else {
      return <Text className="" label={rowData?.applicant?.loan?.loanStatus ?? "-"} />;
    }
  };

  const renderDateOfSearch = (rowData?: any) => {
    return <Text className="" label={formatDateWithTime(rowData?.dateOfSearch)} />;
  };

  const renderPreviousStatus = (rowData?: any) => {
    return <Text className="" label={PREVIOUS_STATUS[`${rowData?.previousStatus}`] ?? "-"} />;
  };

  const renderNameSearchMatchAction = (rowData?: any) => {
    return (
      <SimpleDropDown
        name={`action`}
        className="!w-32"
        options={[...DEDUPE_CHECK]}
        optionLabel={"label"}
        optionValue={"value"}
        value={rowData.action ?? ""}
        onChange={(e: DropdownChangeParams) => {
          const rowDataObj = { ...rowData };
          rowDataObj.action = e.value;
          nameSearchMatchTableRef.current?.reportDataChange(rowDataObj);
        }}
        disabled={pageData.nameSearch.nameSearchPageMode === PAGE_MODE.VIEW}
      />
    );
  };

  const renderDocumentType = (rowData?: any) => {
    if (rowData?.kyc) {
      return <Text className="" label={rowData?.kyc?.docType?.name ?? "-"} />;
    } else {
      return <Text className="" label={rowData.documentTypeName ?? "-"} />;
    }
  };

  const renderDocumentNo = (rowData?: any) => {
    if (rowData?.kyc) {
      return <Text className="" label={rowData?.kyc?.docNo ?? "-"} />;
    } else {
      return <Text className="" label={rowData?.documentNo ?? "-"} />;
    }
  };

  const renderKYCSearchAction = (rowData?: any) => {
    return (
      <TextButton
        className={"text-primary-copyTextColor"}
        label={"Search"}
        onClick={() => {
          onSearchKycDocForDedupe(rowData);
        }}
        disabled={pageData.kycSearch.kycSearchPageMode === PAGE_MODE.VIEW}
      />
    );
  };

  const renderKYCSearchActionHeader = (rowData?: any) => {
    return (
      <div className="flex justify-end items-center w-full">
        <Text label={"label.dedupe.action"} className="" />
      </div>
    );
  };

  const renderKYCSearchMatchAction = (rowData?: any) => {
    return (
      <SimpleDropDown
        name={`action`}
        className="!w-32"
        options={[...DEDUPE_CHECK]}
        optionLabel="label"
        optionValue="value"
        value={rowData.action ?? ""}
        onChange={(e: DropdownChangeParams) => {
          const rowDataObj = { ...rowData };
          rowDataObj.action = e.value;
          kycSearchMatchTableRef.current?.reportDataChange(rowDataObj);
        }}
        disabled={pageData.kycSearch.kycSearchPageMode === PAGE_MODE.VIEW}
      />
    );
  };

  const renderLoanAppNo = (rowData?: any) => {
    return <Text className="" label={rowData?.lanId ?? "-"} />;
  };

  const renderAppType = (rowData?: any) => {
    if (rowData?.kyc) {
      return <Text className="" label={rowData?.kyc?.applicant?.applicantType ?? "-"} />;
    } else if (rowData?.applicant) {
      return <Text className="" label={rowData?.applicant?.applicantType ?? "-"} />;
    }
  };

  const renderGender = (rowData?: any) => {
    if (rowData?.kyc) {
      return <Text className="" label={GENDER[`${rowData?.kyc?.applicant?.smGender}`] ?? "-"} />;
    } else if (rowData?.applicant) {
      return <Text className="" label={GENDER[`${rowData?.applicant?.smGender}`] ?? "-"} />;
    }
  };

  const renderNameSearch = (currentTabTableData: IApplicantInfo[]) => {
    return (
      <div className="flex flex-col justify-center items-start space-y-4">
        <div className="w-full">
          <Text
            className="text-primary-ptext-1000 text-xs"
            label={"label.section.nameSearch.nameAndMobileDetails"}
          />
        </div>

        <ReadOnlyTable
          value={currentTabTableData}
          className="selected-existing-doc-read-table !min-h-auto"
          ref={nameSearchTableRef}
          dataKey="uid"
        >
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={textConverter("label.section.nameSearch.firstName")}
            body={renderFirstName}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={textConverter("label.section.nameSearch.middleName")}
            body={renderMiddleName}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={textConverter("label.section.nameSearch.lastName")}
            body={renderLastName}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={textConverter("label.section.nameSearch.mobileNo")}
            body={renderMobileNo}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={textConverter("label.dedupe.action")}
            body={renderNameSearchAction}
          />
        </ReadOnlyTable>

        <div className="w-full">
          <Text className="text-primary-ptext-1000 text-xs" label={"label.dedupe.matchResults"} />
        </div>

        {currentApplicantInfo?.uid && (
          <div className="w-full">
            <EditableTableV2
              ref={nameSearchMatchTableRef}
              standardDataFormatPromise={DedupeService.getCustomerForDedupe}
              search={`&linkedApplicantUid=${currentApplicantInfo.uid}&linkedLanId=${currentApplicantInfo.lanId}&type=Applicant`}
              paginator
              uniqueKey="uid"
              mode={"UPDATE"}
            >
              <Column
                className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.loanApplicationNo")}
                body={renderLoanAppNo}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.applicantType")}
                body={renderAppType}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.firstName")}
                body={renderFirstName}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.middleName")}
                body={renderMiddleName}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.lastName")}
                body={renderLastName}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                header={textConverter("label.section.kycSearch.gender")}
                body={renderGender}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.mobileNo")}
                body={renderMobileNo}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.matchingList")}
                body={renderMatchingList}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.fileStatus")}
                body={renderFileStatus}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.dateofSearch")}
                body={renderDateOfSearch}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.prevStatus")}
                body={renderPreviousStatus}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6 pr-3 justify-end last-column-sort-icon-position"
                frozen={!isMobile}
                alignFrozen="right"
                header={textConverter("label.dedupe.status")}
                body={renderNameSearchMatchAction}
                style={{ minWidth: "50px" }}
              />
            </EditableTableV2>
          </div>
        )}
      </div>
    );
  };

  const renderKYCSearch = () => {
    return (
      <div className="flex flex-col justify-center items-start space-y-4">
        <div className="w-full">
          <Text
            className="font-bold text-primary-ptext-1000 text-xs"
            label={"label.section.kycSearch.kycDetails"}
          />
        </div>

        <ReadOnlyTable
          value={currentApplicantKycDocsInfo}
          className="selected-existing-doc-read-table !min-h-auto"
          ref={kycSearchTableRef}
          dataKey="uid"
        >
          <Column
            className="text-primary-ptext-1000 text-left text-xs h-12 pl-6"
            header={textConverter("label.section.kycSearch.documentType")}
            body={renderDocumentType}
          />

          <Column
            className="text-primary-ptext-1000 text-left text-xs h-12 pl-6"
            header={textConverter("label.section.kycSearch.documentNo")}
            body={renderDocumentNo}
          />
          <Column
            className="text-primary-ptext-1000 text-end text-xs px-6 h-12 custom-header"
            header={renderKYCSearchActionHeader()}
            body={renderKYCSearchAction}
          />
        </ReadOnlyTable>

        <div className="w-full">
          <Text className="text-primary-ptext-1000 text-xs" label={"label.dedupe.matchResults"} />
        </div>

        {activeKycDocForDedupe.uid ? (
          <div className="w-full">
            <EditableTableV2
              ref={kycSearchMatchTableRef}
              standardDataFormatPromise={DedupeService.getCustomerKycForDedupe}
              search={`&linkedKycUid=${activeKycDocForDedupe.uid}&linkedLanId=${activeKycDocForDedupe.lanId}&type=Kyc`}
              paginator
              uniqueKey="uid"
              mode={"UPDATE"}
            >
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={textConverter("label.section.kycSearch.loanApplicationNo")}
                body={renderLoanAppNo}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={textConverter("label.section.kycSearch.applicantType")}
                body={renderAppType}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.firstName")}
                body={renderFirstName}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.middleName")}
                body={renderMiddleName}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.lastName")}
                body={renderLastName}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={textConverter("label.section.kycSearch.gender")}
                body={renderGender}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={textConverter("label.section.kycSearch.documentType")}
                body={renderDocumentType}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={textConverter("label.section.kycSearch.documentNo")}
                body={renderDocumentNo}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={textConverter("label.section.kycSearch.matchingList")}
                body={renderMatchingList}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={textConverter("label.section.kycSearch.fileStatus")}
                body={renderFileStatus}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={textConverter("label.section.kycSearch.dateOfSearch")}
                body={renderDateOfSearch}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
                header={textConverter("label.section.nameSearch.prevStatus")}
                body={renderPreviousStatus}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6 pr-3"
                header={textConverter("label.dedupe.status")}
                frozen={!isMobile}
                alignFrozen="right"
                body={renderKYCSearchMatchAction}
                style={{ minWidth: "50px" }}
              />
            </EditableTableV2>
          </div>
        ) : (
          <ReadOnlyTable className="!min-h-auto" value={[]} ref={null} dataKey="uid">
            <Column
              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
              header={textConverter("label.section.kycSearch.loanApplicationNo")}
            />
            <Column
              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
              header={textConverter("label.section.kycSearch.applicantType")}
            />
            <Column
              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
              header={textConverter("label.section.nameSearch.firstName")}
            />
            <Column
              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
              header={textConverter("label.section.nameSearch.middleName")}
            />
            <Column
              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
              header={textConverter("label.section.nameSearch.lastName")}
            />
            <Column
              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
              header={textConverter("label.section.kycSearch.gender")}
            />
            <Column
              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
              header={textConverter("label.section.kycSearch.documentType")}
            />
            <Column
              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
              header={textConverter("label.section.kycSearch.documentNo")}
            />
            <Column
              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
              header={textConverter("label.section.kycSearch.matchingList")}
            />
            <Column
              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
              header={textConverter("label.section.kycSearch.fileStatus")}
            />
            <Column
              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
              header={textConverter("label.section.kycSearch.dateOfSearch")}
            />
            <Column
              className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
              header={textConverter("label.section.nameSearch.prevStatus")}
            />
            <Column
              className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6 pr-2"
              header={textConverter("label.dedupe.status")}
            />
          </ReadOnlyTable>
        )}
      </div>
    );
  };

  const renderFinalStatus = () => {
    const { isFinalStatusLoading, finalStatusPageMode, status, statusEditorData } =
      pageData.finalStatus;

    return isFinalStatusLoading ? (
      <SkeletonCustom height="19rem" width="100%" borderRadius="8px" animation="wave" />
    ) : (
      <div className="flex flex-col justify-center items-start w-full gap-y-3 mt-3">
        <div className="w-62.5">
          <SimpleDropDown
            label={"label.section.finalStatus.status*"}
            name="status"
            value={status}
            onChange={handleStatusChange}
            options={[...CURRENT_STAGE_STATUS]}
            optionLabel="label"
            optionValue="value"
            onBlur={validate}
            validation={simpleValidator?.current?.message(`status`, status, "mandatory")}
            disabled={pageData.finalStatus.finalStatusPageMode === PAGE_MODE.VIEW}
          />
        </div>

        <RichTextEditor
          ref={statusEditorRef}
          initialValue={statusEditorData}
          disabled={finalStatusPageMode === PAGE_MODE.VIEW}
        />
      </div>
    );
  };

  const renderContent = (customerDedupe: ICustomerTabInfo) => {
    const getCurrentTabLoanInfo = pageData.nameSearch.applicantsInLoan.filter(
      (applicant) => applicant.type === customerDedupe.tabType
    );

    return (
      <div className="mt-4" key={customerDedupe?.key}>
        <Section>
          <div className="flex flex-col gap-4">
            <SaveComponent
              pageMode={pageData.nameSearch.nameSearchPageMode}
              title={"heading.section.nameAndMobileSearch"}
              onCancel={onCancelNameSearch}
              onEdit={onEditNameSearch}
              onSave={onSaveNameSearchMatchedResults}
            >
              {renderNameSearch(getCurrentTabLoanInfo)}
            </SaveComponent>

            <SaveComponent
              pageMode={pageData.kycSearch.kycSearchPageMode}
              title={"heading.section.kycSearch"}
              onCancel={onCancelKYCSearch}
              onEdit={onEditKYCSearch}
              onSave={onSaveKycSearchMatchedResults}
            >
              {renderKYCSearch()}
            </SaveComponent>

            <SaveComponent
              pageMode={pageData.finalStatus.finalStatusPageMode}
              title={"heading.section.finalStatus"}
              onCancel={onCancelFinalStatus}
              onEdit={onEditFinalStatus}
              onSave={onSaveFinalStatus}
            >
              {renderFinalStatus()}
            </SaveComponent>
          </div>
        </Section>
      </div>
    );
  };

  /* 
    -----------------------------------------------------------------------------------------
    render: The Main return()
    -----------------------------------------------------------------------------------------
  */
  if (isPageLoading) {
    return <Loader />;
  }

  const isMobile = window.innerWidth <= 768;

  return (
    <React.Fragment>
      <SimpleTab
        className="simple-tab-custom"
        showTooltip
        optionLabel={["tabName"]}
        optionKey={["key"]}
        headerClassName=""
        ref={tabRef}
        activeIndex={activeIndex}
        tabs={customerTabsListForDedupe}
        onTabChange={handleTabChange}
        renderContent={renderContent}
        scrollable
      />

      <ToastCustom
        position={position}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={toastMessageVariant}
      />

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </React.Fragment>
  );
};

export default PageCustomerDedupe;
